/* Header.css */

.header {
    background-color: #556B2F; /* Dark Green Background Color */
    color: #fff; /* Text Color */
    text-align: center;
    border-style: solid;
    border-color: #ffa500;
    padding: 40px 0;
    border-top-left-radius: 10px; /* Round the top-left corner */
    border-top-right-radius: 10px; /* Round the top-right corner */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  }
  
  .header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px; /* Adds spacing between the image and text */
  }
  
  .profile-image {
    width: 150px; /* Adjust the width of the profile image as needed */
    height: 150px; /* Adjust the height of the profile image as needed */
    border-radius: 50%; /* Makes the image circular */
    object-fit: cover; /* Maintains aspect ratio and covers the container */
  }
  
  .header h1 {
    font-size: 34px; /* Font size for the name */
    margin: 0; /* Remove margin to align with the image */
    color: #ffa500;
  }
  
  .header p {
    font-size: 18px;
    text-align: center; /* Center-align the paragraph element */
    margin: 0; /* Remove margin to align with the image */
  }